.section {
  width: 100%;

  margin: 0 auto;
  padding-bottom: 25px;
}

// <p>
.uppertitle {
  display: flex;
  align-items: center;

  margin-bottom: 16px;

  font-family: var(--font-family-title);
  font-size: 18px;
  line-height: 1.167;
  text-transform: uppercase;

  color: var(--cl-txt-accent-light);

  @include mq(tablet) {
    font-size: 26px;
    line-height: 1.15;
  }

  @include mq(desktop) {
    font-size: 30px;
    line-height: 1.13;
  }
}

// <h2>
.section-title {
  font-family: var(--font-family-title);
  font-size: 34px;
  line-height: 1.147;
  text-transform: uppercase;

  @include mq(tablet) {
    font-size: 48px;
    line-height: 1.146;
    letter-spacing: 0.06em;
  }

  @include mq(desktop) {
    font-size: 58px;
    line-height: 1.138;
  }
}

// <p>
.subtitle {
  margin-bottom: 40px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;

  @include mq(tablet) {
    font-size: 14px;
  }

  @include mq(desktop) {
    font-size: 16px;
  }
}

// <p>
.description {
  margin-bottom: 40px;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;

  @include mq(tablet) {
    font-size: 14px;
    line-height: 1.174;
  }

  @include mq(desktop) {
    font-size: 16px;
    line-height: 1.76;
    letter-spacing: 0.02em;
  }
}
